#skills {
  margin-top: -4rem;
}
#skills h2 {
  font-size: xx-large;
}
.experience_container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  gap: 2rem;
}
.experience_container > div {
  background: var(--color-bg-variant);
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.experience_container > div:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.experience_container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.experience_content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
  margin-top: 1rem;
}

.skills-card {
  display: flex;
  gap: 1rem;
}

.skills-card .skills-card-img {
  margin-top: 6px;
  /* color: var(--color-primary); */
}
.skills-card-img {
  font-size: 25px;
}

/* ================================== medium screen ============================ */

@media screen and (max-width: 1024px) {
  #skills {
    margin-top: 10rem;
  }
  .experience_container {
    grid-template-columns: 1fr;
  }
  .experience_container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }
  .experience_content {
    padding: 1rem;
  }
}

/* ================================== small screen ============================ */

@media screen and (max-width: 600px) {
  #skills {
    margin-top: 5rem;
  }

  .experience_container {
    gap: 2rem;
  }
  .experience_container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
}
